import React from 'react';

export default class Home extends React.Component {
	render() {
		return (
			<div
				style={{
					position: 'fixed',
					top: '45%',
					left: '50%',
					/* bring your own prefixes */
					transform: 'translate(-50%, -50%)',
					fontSize: '35px',
					fontFamily: 'Source Code Pro',
					textAlign: "center"
				}}
			>
				under construction, check out <a href="/reports">reports</a>
			</div>
		);
	}
}
