import React from 'react';
import axios from 'axios';
import {ListGroup, Button, InputGroup, FormControl} from 'react-bootstrap';
import {BoxArrowInUpRight} from 'react-bootstrap-icons';
import {Scrollbars} from 'react-custom-scrollbars';

Date.prototype.shortDate = function () {
	return (
		('0' + this.getDate()).slice(-2) +
		'/' +
		('0' + (this.getMonth() + 1)).slice(-2) +
		'/' +
		this.getFullYear()
	);
};

export default class Reports extends React.Component {
	constructor() {
		super();
		this.state = {
			reports: [],
		};
	}
	async componentDidMount() {
		var reports = await axios.get(
			'https://api.hackfeeds.com/api/reports?page=1'
		);
		console.log(reports);
		this.setState({
			reports: reports.data.reports,
		});
	}
	severityColour(severity) {
		switch (severity) {
			case 'critical':
				return 'darkred';
			case 'high':
				return 'red';
			case 'medium':
				return 'orange';
			case 'low':
				return 'yellow';
			default:
				return 'lightblue';
		}
	}
	currency(str) {
		switch (str) {
			case 'USD':
				return '$';
			case 'EUR':
				return '€';
			case 'GBP':
				return '£';
			default:
				return str + ' ';
		}
	}
	bounty(report) {
		if (report.bounty.amount) {
			return (
				this.currency(report.bounty.currency) +
				report.bounty.amount.toLocaleString()
			);
		} else {
			return '$0.00';
		}
	}

	render() {
		return (
			<div>
				<div
					style={{
						width: '75%',
						marginLeft: '12.5%',
						marginTop: '1%',
						position: 'absolute',
						fontFamily: 'Source Code Pro',
					}}
				>
					{/* SEARCH/FILTER AREA */}
					<div>
						<InputGroup className="mb-3">
							<FormControl
								placeholder="Search..."
								aria-label="search"
								aria-describedby="basic-addon1"
							/>
						</InputGroup>
					</div>
					{/* LIST AREA */}
					<Scrollbars
						renderTrackVertical={() => {
							return <div></div>;
						}}
						style={{
							borderRadius: '5px',
							border: '1px solid rgba(0,0,0,.125)',
							borderBottom: '1px solid rgba(0,0,0,.125)',
							width: 'auto',
							height: '85vh',
						}}
					>
						<ListGroup defaultActiveKey="#">
							{this.state.reports.map((r, i) => {
								return (
									<ListGroup.Item
										style={{
											borderRight:
												'solid ' + this.severityColour(r.severity) + ' 5px',
											borderLeft: '0',
											borderTop: '0',
											wordWrap: 'break-word',
											height: '125px',
											display: 'flex',
											alignItems: 'center',
										}}
										action
										key={i}
										onClick={() => {
											console.log(1);
										}}
									>
										{/* LEFT FLOAT */}
										<div>
											<span
												style={{
													display: 'block',
													wordWrap: 'break-word',
													fontSize: 18.5,
												}}
											>
												{r.title}
											</span>
											<div style={{fontSize: 15, marginTop: '1%'}}>
												<a
													href={r.author.url}
													target="_blank"
													style={{color: 'inherit'}}
												>
													{r.author.username || 'Anonymous'}
												</a>{' '}
												| {this.bounty(r)} |{' '}
												<a
													href={r.program.url}
													target="_blank"
													style={{color: 'inherit'}}
												>
													{r.program.name} 
												</a> |{' '}
												{new Date(r.disclosure_date).shortDate()}
											</div>
										</div>
										{/* RIGHT FLOAT */}
										<Button
											variant="outline"
											style={{
												float: 'right',

												marginLeft: 'auto',
											}}
											href={r.url}
											target="_blank"
										>
											<BoxArrowInUpRight size={20} />
										</Button>
									</ListGroup.Item>
								);
							})}
						</ListGroup>
					</Scrollbars>
				</div>
			</div>
		);
	}
}
